import * as React from 'react';
import { useFormik } from 'formik';
import { useNavigate } from 'react-router-dom';
import { useState } from 'react';
import { Icon } from '@iconify/react';
import plusFill from '@iconify/icons-eva/plus-fill';
import { useSnackbar } from 'notistack';
// material
import {
  Container,
  CircularProgress,
  Stack,
  Typography,
  Avatar,
  Button,
  IconButton
} from '@mui/material';
import { makeStyles } from '@mui/styles';
import TextField from '@mui/material/TextField';
import Divider from '@mui/material/Divider';
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';
import Grid from '@mui/material/Grid';
import Dialog from '@mui/material/Dialog';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import { deepPurple } from '@mui/material/colors';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import HighlightOffIcon from '@mui/icons-material/HighlightOff';
// components
import Page from '../../components/Page';
import {
  ProjectSort,
  ProjectList,
  Widget,
  ProjectFilterSidebar
} from '../../components/_dashboard/projects';
import AddProject from './AddProject';
//
import PRODUCTS from '../../_mocks_/products';
import { projectListing, deleteProject } from '../../DAL/Project/Project';

// ----------------------------------------------------------------------

// ============================================ styling=========================

const useStyles = makeStyles(() => ({
  loading: {
    marginLeft: '50%',
    marginTop: '20%'
  }
}));

export default function ProjectListing() {
  const classes = useStyles();
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();
  const [iswaiting, setIswaiting] = useState(true);
  const [openFilter, setOpenFilter] = useState(false);
  const [opendialog, setOpenDialog] = React.useState(false);
  const [data, setData] = React.useState([]);

  const formik = useFormik({
    initialValues: {
      gender: '',
      category: '',
      colors: '',
      priceRange: '',
      rating: ''
    },
    onSubmit: () => {
      setOpenFilter(false);
    }
  });

  const { resetForm, handleSubmit } = formik;

  const handleOpenDialig = () => {
    setOpenDialog(true);
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
  };

  const handleOpenFilter = () => {
    setOpenFilter(true);
  };

  const handleCloseFilter = () => {
    setOpenFilter(false);
  };

  const handleResetFilter = () => {
    handleSubmit();
    resetForm();
  };

  const projectDelete = async (id) => {
    const formData = new FormData();
    formData.append('token', localStorage.getItem('token'));
    formData.append('project_id', id);
    const result = await deleteProject(formData);
    if (result.code === 200) {
      fetchListing();
    } else {
      enqueueSnackbar(result.message, { variant: 'error' });
    }
  };

  const fetchListing = async () => {
    const formData = new FormData();
    formData.append('token', localStorage.getItem('token'));
    const result = await projectListing(formData);
    if (result.code === 200) {
      setData(result.Project_list);
      setIswaiting(false);
    } else {
      setIswaiting(false);
      enqueueSnackbar(result.message, { variant: 'error' });
    }
  };

  React.useEffect(() => {
    fetchListing();
  }, []);

  if (iswaiting) {
    return <CircularProgress className={classes.loading} color="primary" />;
  }

  return (
    <Page title="Projects | Support Task Portal">
      <Container>
        <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
          <Typography variant="h4">Projects</Typography>
          <Button
            variant="contained"
            onClick={() => navigate('/project/add_project')}
            startIcon={<Icon icon={plusFill} />}
          >
            New Project
          </Button>
        </Stack>
        <ProjectList projects={data} onDeleteProject={projectDelete} />

        {/* ============================================== Add Project component========================= */}

        <Dialog open={opendialog} onClose={handleCloseDialog}>
          <IconButton onClick={handleCloseDialog} style={{ marginLeft: 'auto' }}>
            <HighlightOffIcon />
          </IconButton>
          <DialogTitle style={{ textAlign: 'center' }}>Add Project</DialogTitle>
          <DialogContent>
            <AddProject />
          </DialogContent>
          <DialogActions>{/* <Button onClick={handleCloseDialog}>Cancel</Button> */}</DialogActions>
        </Dialog>
      </Container>
    </Page>
  );
}
