import React, { useEffect, useState, useRef } from 'react';
import TextField from '@mui/material/TextField';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import Divider from '@mui/material/Divider';
import ListItemText from '@mui/material/ListItemText';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Avatar from '@mui/material/Avatar';
import AddIcon from '@mui/icons-material/Add';
import MenuList from '@mui/material/MenuList';
import MenuItem from '@mui/material/MenuItem';
import { Typography, AvatarGroup, IconButton, Tooltip, Popover } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import { baseUri } from '../../../config/config';
// components
import MenuPopover from '../../../components/MenuPopover';

function MembersListing(props) {
  const anchorRef = useRef(null);
  const [openMemberListing, setOpenMemberListing] = useState(false);

  const handleClose = () => {
    setOpenMemberListing(false);
  };

  const handleOpenMemberListing = () => {
    setOpenMemberListing(true);
  };

  const handleAdd = (value) => {
    props.onAddMember(value, props.onId);
  };

  const handleDelete = (value) => {
    props.onDeleteMember(value, props.onId);
  };

  const getSelectedMember = (team) => {
    const updatedList = props.membersData.map((x, i) => {
      const findMember = team.find((t) => String(x.user_id) === String(t.user_id));
      if (findMember) {
        return {
          ...x,
          isSelected: true
        };
      }

      return {
        ...x,
        isSelected: false
      };
    });
    return updatedList;
  };

  return (
    <>
      <Tooltip title="Assignees">
        <AvatarGroup
          max={3}
          style={{ cursor: 'pointer' }}
          ref={anchorRef}
          onClick={() => handleOpenMemberListing()}
        >
          {props.memberslist.map((z, k) => (
            <>
              {z.image !== '' ? (
                <Avatar
                  alt="Remy Sharp"
                  src={baseUri + z.image}
                  style={{ height: 30, width: 30 }}
                />
              ) : (
                <Avatar alt="Remy Sharp" style={{ height: 30, width: 30 }}>
                  {z.first_name.substring(0, 1)}
                </Avatar>
              )}
            </>
          ))}
        </AvatarGroup>
      </Tooltip>
      <MenuPopover open={openMemberListing} onClose={handleClose} anchorEl={anchorRef.current}>
        <List>
          <ListItem>
            <ListItemText primary="Assignees" />
          </ListItem>
          {props.memberslist.length > 0
            ? props.memberslist.map((y, j) => (
                <>
                  <ListItem>
                    <ListItemIcon>
                      {y.image !== '' ? (
                        <Avatar
                          alt="Remy Sharp"
                          src={baseUri + y.image}
                          style={{ height: 30, width: 30 }}
                        />
                      ) : (
                        <Avatar alt="Remy Sharp" style={{ height: 30, width: 30 }}>
                          {y.first_name.substring(0, 1)}
                        </Avatar>
                      )}
                    </ListItemIcon>
                    <ListItemText>
                      {y.first_name} {y.last_name}
                    </ListItemText>
                  </ListItem>
                </>
              ))
            : ''}
        </List>
      </MenuPopover>
    </>
  );
}

export default MembersListing;
