import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  TextField,
  Grid,
  CircularProgress,
  IconButton,
  Button,
  Typography
} from '@mui/material';
import Switch from '@mui/material/Switch';
import { makeStyles } from '@mui/styles';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Chip from '@mui/material/Chip';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { useSnackbar } from 'notistack';
import DriveFolderUploadIcon from '@mui/icons-material/DriveFolderUpload';
import { addProject, activeTeamList } from '../../DAL/Project/Project';
import { teamList } from '../../DAL/Team/Team';
import { useSocket } from '../../Hooks/socketContext';

const names = [
  'Oliver Hansen',
  'Van Henry',
  'April Tucker',
  'Ralph Hubbard',
  'Omar Alexander',
  'Carlos Abbott',
  'Miriam Wagner',
  'Bradley Wilkerson',
  'Virginia Andrews',
  'Kelly Snyder'
];

// ============================================ styling=========================

const useStyles = makeStyles(() => ({
  loading: {
    marginLeft: '50%',
    marginTop: '20%'
  }
}));

function AddProject(props) {
  const { recieveSocketNotification, sendSocketNotification, showSocketNotification } = useSocket();
  const classes = useStyles();
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();
  const [iswaiting, setIswaiting] = useState(false);
  const [name, setName] = useState('');
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [status, setStatus] = useState(true);
  const [description, setDescription] = useState('');
  const [member, setMember] = useState([]);
  const [membersData, setMembersData] = useState([]);
  const [Image, setimage] = React.useState('');
  const [file, setProfileImage] = React.useState('');

  const handleChange = (event) => {
    const {
      target: { value }
    } = event;
    setMember(
      // On autofill we get a the stringified value.
      typeof value === 'string' ? value.split(',') : value
    );
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    let _status;
    if (status === true) {
      _status = 1;
    } else {
      _status = 0;
    }
    setIswaiting(true);
    const formData = new FormData();
    formData.append('token', localStorage.getItem('token'));
    formData.append('project_name', name);
    formData.append('start_date', startDate);
    formData.append('end_date', endDate);
    formData.append('project_description', description);
    formData.append('status', _status);
    if (Image) {
      formData.append('project_image', Image);
    }
    const arr = member.map((x, i) => {
      formData.append(`team[${i}]`, x.user_id);
      return x;
    });
    const result = await addProject(formData);
    if (result.code === 200) {
      setIswaiting(false);
      navigate(-1);
      member.map((z, k) => {
        const payload = {
          reciever_id: z.user_id,
          message: `You are added in ${name} project`,
          project_id: result.project_data.id,
          project_name: result.project_data.project_name
        };
        sendSocketNotification(payload);
        return z;
      });
    } else {
      setIswaiting(false);
      enqueueSnackbar(result.message, { variant: 'error' });
    }
  };

  const fileChangedHandler = async (e) => {
    // setiswaiting(true);
    setProfileImage(URL.createObjectURL(e.target.files[0]));
    setimage(e.target.files[0]);
    // let formData = new FormData(); //formdata object
    // formData.append("directory", "images/");
    // formData.append("image", e.target.files[0]);
    // const upload_image_resp = await upload_image(formData);
    // console.log(upload_image_resp);
    // setimage(upload_image_resp.path);
    // if(upload_image_resp.code == 200){
    //   setiswaiting(false);
    // }
  };

  useEffect(async () => {
    const formData = new FormData();
    formData.append('token', localStorage.getItem('token'));
    const result = await activeTeamList(formData);
    if (result.code === 200) {
      setMembersData(result.Team_member_list);
    } else {
      enqueueSnackbar(result.message, { variant: 'error' });
    }
  }, []);

  if (iswaiting) {
    return <CircularProgress className={classes.loading} color="primary" />;
  }

  return (
    <>
      <Container maxWidth="sm">
        <IconButton onClick={() => navigate(-1)}>
          <ArrowBackIcon />
        </IconButton>
        <Typography variant="h4" sx={{ mb: 5 }} style={{ textAlign: 'center' }}>
          Add Project
        </Typography>
        <form onSubmit={handleSubmit}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <TextField
                autoFocus
                required
                margin="dense"
                value={name}
                onChange={(e) => setName(e.target.value)}
                id="name"
                label="Name"
                type="name"
                fullWidth
                variant="outlined"
              />
            </Grid>
            <Grid item xs={6}>
              <TextField
                margin="dense"
                value={startDate}
                onChange={(e) => setStartDate(e.target.value)}
                id="name"
                label="Start Date"
                type="date"
                fullWidth
                variant="outlined"
                required
                InputLabelProps={{
                  shrink: true
                }}
              />
            </Grid>
            <Grid item xs={6}>
              <TextField
                margin="dense"
                required
                value={endDate}
                onChange={(e) => setEndDate(e.target.value)}
                id="name"
                label="End Date"
                type="date"
                fullWidth
                variant="outlined"
                InputLabelProps={{
                  shrink: true
                }}
              />
            </Grid>
            <Grid item xs={12}>
              <FormControl fullWidth>
                <InputLabel id="demo-multiple-chip-label">Team Member</InputLabel>
                <Select
                  labelId="demo-multiple-chip-label"
                  id="demo-multiple-chip"
                  multiple
                  value={member}
                  onChange={handleChange}
                  input={<OutlinedInput id="select-multiple-chip" label="Team Member" />}
                  renderValue={(selected) => (
                    <>
                      {selected.map((value) => (
                        <Chip
                          key={value.id}
                          label={`${value.first_name}${' '} ${value.last_name}`}
                        />
                      ))}
                    </>
                  )}
                >
                  {membersData.map((x, i) => (
                    <MenuItem key={i} value={x}>
                      {x.first_name} {(' ', x.last_name)}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12}>
              <TextField
                margin="dense"
                value={description}
                onChange={(e) => setDescription(e.target.value)}
                id="name"
                label="Description"
                type="description"
                fullWidth
                variant="outlined"
                multiline
                rows={4}
              />
            </Grid>
            <Grid item xs={12}>
              {file ? (
                <>
                  {' '}
                  <img className={classes.img} src={file} alt="team member" />{' '}
                </>
              ) : null}
            </Grid>
            <Grid item xs={12}>
              <input
                accept="image/x-png,image/jpeg"
                hidden
                id="contained-button-file"
                multiple
                type="file"
                onChange={fileChangedHandler}
              />
              <label htmlFor="contained-button-file" style={{ display: 'flex' }}>
                <Button
                  component="span"
                  fullWidth
                  variant="outlined"
                  color="primary"
                  startIcon={<DriveFolderUploadIcon />}
                >
                  Upload Image
                </Button>
              </label>
            </Grid>
            <Grid item xs={6}>
              Status :
              <Switch
                checked={status}
                onChange={(e) => setStatus(e.target.checked)}
                inputProps={{ 'aria-label': 'controlled' }}
              />
            </Grid>
            <Grid item xs={12} style={{ textAlign: 'center' }}>
              <Button variant="contained" type="submit">
                Add Project
              </Button>
            </Grid>
          </Grid>
        </form>
      </Container>
    </>
  );
}

export default AddProject;

