import React, { useContext, useEffect, useState } from 'react';
// import io from 'socket.io-client';
import socketio from 'socket.io-client';
import { useNavigate } from 'react-router-dom';
import { useSnackbar } from 'notistack';
import { socketUrl } from '../config/config';
import {
  teamNotification,
  teamNotificationStatusChange,
  teamNotificationAllRead
} from '../DAL/Notification/Notification';

const SocketContext = React.createContext();

export const useSocket = () => useContext(SocketContext);
let socket;
export function ContextSocket({ children }) {
  /* ------------------------------------------------------
  ------------------| States |--------------------
  ------------------------------------------------------- */
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();
  const [notifications, setNotifications] = useState([]);

  /* ------------------------------------------------------
  ------------------/ Hooks Functions /--------------------
  ------------------------------------------------------- */

  const fetchNotificationListing = async () => {
    const formData = new FormData();
    formData.append('token', localStorage.getItem('token'));
    const result = await teamNotification(formData);
    if (result.code === 200) {
      console.log('notification', result);
      setNotifications(result.notify_list);
    } else {
      enqueueSnackbar(result.message, { variant: 'error' });
    }
  };

  const showNotification = (_data) => {
    const notification = new Notification('New Notification: Dynamite Digital CheckList!', {
      body: _data.payload,
      icon: 'https://cdn0.iconfinder.com/data/icons/small-n-flat/24/678134-sign-check-512.png'
    });

    notification.onclick = function (event) {
      event.preventDefault();
      if (_data.task_id) {
        navigate(`/project/${_data.project_id}`, {
          state: { task_id: _data.task_id, project_name: _data.project_name }
        });
      } else {
        navigate(`/project/${_data.project_id}`, {
          state: { project_name: _data.project_name }
        });
      }
    };
  };

  const showSocketNotification = (_data) => {
    if (Notification.permission === 'granted') {
      console.log('granted');
      fetchNotificationListing();
      showNotification(_data);
    } else if (Notification.permission !== 'denied') {
      Notification.requestPermission().then((permission) => {
        if (permission === 'granted') {
          showNotification(_data);
          fetchNotificationListing();
        }
      });
    }
  };

  const sendSocketNotification = (payload) => {
    console.log(payload, 'payload');
    const data = {
      sender_id: localStorage.getItem('user_id'),
      reciever_id: payload.reciever_id,
      project_id: payload.project_id,
      // task_id: payload.task_id,
      project_name: payload.project_name,
      payload: payload.message
    };
    if (payload.task_id) {
      data.task_id = payload.task_id;
    }
    socket.emit('send_notification_to_all', data);

    console.log(socket, 'send');
  };

  useEffect(() => {
    const connectionOptions = {
      query: {},
      'force new connection': true,
      reconnection: true,
      autoConnect: true,
      reconnectionDelay: 2000,
      reconnectionDelayMax: 60000,
      reconnectionAttempts: 'Infinity',
      timeout: 10000,
      transports: ['websocket']
    };
    socket = socketio(socketUrl);
    // socket = io(socketUrl);
    socket.emit('create', localStorage.getItem('user_id'));

    socket.off('receiver_notification').on('receiver_notification', (data) => {
      console.log(data, 'receiver_notification');

      if (String(data.reciever_id) === String(localStorage.getItem('user_id'))) {
        console.log(
          data.reciever_id,
          'reciever_id',
          localStorage.getItem('user_id'),
          'localStorage'
        );
        showSocketNotification(data);
      }
    });

    console.log(socket, 'socket connection');
  }, [socketUrl]);

  console.log(children, 'children show');

  useEffect(() => {}, []);

  const collection = {
    sendSocketNotification,
    showSocketNotification,
    fetchNotificationListing,
    notifications
  };
  return <SocketContext.Provider value={collection}>{children}</SocketContext.Provider>;
}
