import React from 'react';
import { Routes, Route, Navigate } from 'react-router-dom';
import {
  PrivateRouteWithLayout,
  // RouteWithLayout,
  PublicRouteWithLayout
  // PrivateRouteWithLayout,
} from './components';
// layouts
import HomeLayout from './layouts/HomeLayout';
import DashboardLayout from './layouts/dashboard';
import LogoOnlyLayout from './layouts/LogoOnlyLayout';
//
import {
  ProjectListing,
  AddProject,
  EditProject,
  TaskListing,
  ForgetPassword,
  Team,
  AddTeam,
  EditTeam,
  Departments,
  AddDepartment,
  EditDepartment,
  TeamChangePassword,
  UserProfile
} from './pages/index';
import Login from './pages/Login';
import Register from './pages/Register';
import DashboardApp from './pages/DashboardApp';
import Products from './pages/Products';
import Blog from './pages/Blog';
import User from './pages/User';
import NotFound from './pages/Page404';

// ----------------------------------------------------------------------
const Authentication = () => {
  if (localStorage.getItem('token')) {
    return <Navigate to="/project"> </Navigate>;
  }
  if (!localStorage.getItem('token')) {
    return <Navigate to="/login"> </Navigate>;
  }
};
export default function Router() {
  return (
    <Routes>
      {/* <Route path="/task" element={<HomeLayout />}>
        <Route path="/task" element={<TaskListing />} />
      </Route> */}
      <Route element={<DashboardLayout />}>
        <Route path="/" element={<Authentication />} />
        {/* <Route path="/dashboard" element={<DashboardApp />} /> */}
        <Route path="/project" element={<ProjectListing />} />
        <Route path="/project/add_project" element={<AddProject />} />
        <Route path="/project/edit_project/:id" element={<EditProject />} />
        <Route path="/project/:id" element={<TaskListing />} />
        <Route path="/team" element={<Team />} />
        <Route path="/team/add_team" element={<AddTeam />} />
        <Route path="/team/edit_team/:id" element={<EditTeam />} />
        <Route path="/team/change_password/:id" element={<TeamChangePassword />} />
        <Route path="/department" element={<Departments />} />
        <Route path="/department/add_department" element={<AddDepartment />} />
        <Route path="/department/edit_department/:id" element={<EditDepartment />} />
        <Route path="/user_profile" element={<UserProfile />} />
        {/* <Route path="/user" element={<User />} />
        <Route path="/products" element={<Products />} />
        <Route path="/blog" element={<Blog />} />  */}
      </Route>
      <Route element={<LogoOnlyLayout />}>
        <Route path="/" element={<Authentication />} />
        {/* <Route path="/register" element={<Register />} /> */}
        <Route path="404" element={<NotFound />} />
        <Route path="/login" element={<Login />} />
        <Route path="/forget_password" element={<ForgetPassword />} />
        <Route path="*" element={<NotFound />} />
      </Route>
      {/* <PublicRouteWithLayout component={Login} exact layout={LogoOnlyLayout} path="/login" />
      <PublicRouteWithLayout component={Register} exact layout={LogoOnlyLayout} path="/register" /> */}
      {/* --------------------------------------- Dashboard --------------------------------------- */}
      {/* <PrivateRouteWithLayout
        component={DashboardApp}
        exact
        layout={DashboardLayout}
        path="/dashboard/app"
      /> */}
      {/* --------------------------------------- Change Password --------------------------------------- */}
      {/* <PrivateRouteWithLayout
        component={Products}
        exact
        layout={DashboardLayout}
        path="/dashboard/products"
      /> */}
    </Routes>
  );
}
