import { useRef, useState } from 'react';
// material
import { alpha } from '@mui/material/styles';
import { Box, Chip, IconButton, MenuItem, ListItemIcon, ListItemText } from '@mui/material';
import CircleIcon from '@mui/icons-material/Circle';
import Tooltip from '@mui/material/Tooltip';
// components
import MenuPopover from '../../../components/MenuPopover';

// ----------------------------------------------------------------------

const Status = [
  {
    value: 0,
    label: 'Pending',
    color: 'warning'
    // icon: '/static/icons/ic_flag_en.svg'
  },
  {
    value: 1,
    label: 'In Progress',
    color: 'secondary'
    // icon: '/static/icons/ic_flag_de.svg'
  },
  // {
  //   value: 2,
  //   label: 'In Review',
  //   color: 'warning'
  //   // icon: '/static/icons/ic_flag_fr.svg'
  // },
  {
    value: 3,
    label: 'Completed',
    color: 'primary'
    // icon: '/static/icons/ic_flag_fr.svg'
  },
  {
    value: 4,
    label: 'Cancelled',
    color: 'error'
    // icon: '/static/icons/ic_flag_fr.svg'
  }
];

// ----------------------------------------------------------------------

export default function StatusPopover(props) {
  const anchorRef = useRef(null);
  const [openStatus, setOpenStatus] = useState(false);
  // const [taskstatus, setTaskStatus] = useState(LANGS[0]);

  const handleStatusValue = (e, data) => {
    props.handleStatus(data.value, props.id);
    handleClose();
  };

  const handleClose = () => {
    setOpenStatus(false);
  };

  const handleOpenStatus = () => {
    setOpenStatus(true);
  };

  const getChipColor = (value) => {
    const chip = Status.find((chip) => String(chip.value) === String(value));
    return chip.color;
  };

  const getChipLabel = (value) => {
    const chip = Status.find((chip) => String(chip.value) === String(value));
    return chip.label;
  };

  return (
    <>
      <Tooltip title="Task Status">
        <Chip
          label={String(props.status) === 3 ? Status[3].label : getChipLabel(props.status)}
          color={String(props.status) === 3 ? Status[3].color : getChipColor(props.status)}
          ref={anchorRef}
          onClick={handleOpenStatus}
          style={{ borderRadius: 10, fontSize: 8, height: 25 }}
        />
      </Tooltip>

      <MenuPopover open={openStatus} onClose={handleClose} anchorEl={anchorRef.current}>
        <Box sx={{ py: 1 }}>
          {Status.map((option, i) => (
            <MenuItem
              key={option.value}
              selected={props.status === Status[i].value}
              sx={{ py: 1, px: 2.5 }}
              onClick={(e) => handleStatusValue(e, option)}
            >
              <ListItemIcon style={{ marginRight: 0 }}>
                <CircleIcon color={option.color} />
              </ListItemIcon>
              <ListItemText primaryTypographyProps={{ variant: 'body2', fontSize: '12' }}>
                {option.label}
              </ListItemText>
            </MenuItem>
          ))}
        </Box>
      </MenuPopover>
    </>
  );
}
