import { useRef, useState } from 'react';
// material
import { alpha } from '@mui/material/styles';
import { Box, IconButton, MenuItem, ListItemIcon, ListItemText } from '@mui/material';
import FlagIcon from '@mui/icons-material/Flag';
import Tooltip from '@mui/material/Tooltip';
// components
import MenuPopover from '../../../components/MenuPopover';

// ----------------------------------------------------------------------

const priority = [
  {
    value: 0,
    label: 'Critical',
    color: 'error'
    // icon: '/static/icons/ic_flag_en.svg'
  },
  {
    value: 1,
    label: 'High',
    color: 'primary'
    // icon: '/static/icons/ic_flag_de.svg'
  },
  {
    value: 2,
    label: 'Medium',
    color: 'success'
    // icon: '/static/icons/ic_flag_fr.svg'
  },
  {
    value: 3,
    label: 'Low',
    color: 'info'
    // icon: '/static/icons/ic_flag_fr.svg'
  }
];

// ----------------------------------------------------------------------

export default function PriorityPopover(props) {
  const anchorRef = useRef(null);
  const [openpriority, setOpenPriority] = useState(false);

  const handleClose = () => {
    setOpenPriority(false);
  };

  const handlePriority = (e, data) => {
    props.handleTaskPriority(data.value, props.id);
    handleClose();
  };

  const handleOpenPriority = () => {
    setOpenPriority(true);
  };

  const getFlagColor = (value) => {
    const flag = priority.find((flag) => String(flag.value) === String(value));
    return flag.color;
  };

  const getFlagLabel = (value) => {
    const flag = priority.find((flag) => String(flag.value) === String(value));
    return flag.label;
  };

  return (
    <>
      <Tooltip title={`Priority ${getFlagLabel(props.priority)}`}>
        <IconButton
          size="small"
          style={{ backgroundColor: 'rgba(0, 171, 85, 0.08)' }}
          ref={anchorRef}
          onClick={handleOpenPriority}
        >
          <FlagIcon color={getFlagColor(props.priority)} />
        </IconButton>
      </Tooltip>

      <MenuPopover open={openpriority} onClose={handleClose} anchorEl={anchorRef.current}>
        <Box sx={{ py: 1 }}>
          {priority.map((option, i) => (
            <MenuItem
              key={option.value}
              selected={props.priority === priority[i].value}
              sx={{ py: 1, px: 2.5 }}
              onClick={(e) => handlePriority(e, option)}
            >
              <ListItemIcon style={{ marginRight: 0 }}>
                <FlagIcon color={option.color} />
              </ListItemIcon>
              <ListItemText primaryTypographyProps={{ variant: 'body2', fontSize: '12' }}>
                {option.label}
              </ListItemText>
            </MenuItem>
          ))}
        </Box>
      </MenuPopover>
    </>
  );
}
